



















import { Component, Vue } from 'vue-property-decorator';
import AuthService from '@/services/auth.service';

import axios from 'axios';

const auth = new AuthService();

@Component({
    components: {
    },
})

export default class Home extends Vue {
    public currentUser: string = '';
    public accessTokenExpired: boolean | undefined = false;
    public isLoggedIn: boolean = false;

    public dataEventRecordsItems: [] = [];

    get username(): string {
        return this.currentUser;
    }

    public login() {
        auth.login();
    }

    public logout() {
        auth.logout();
    }

    public mounted() {
        auth.getUser().then((user) => {
            if (user) {
                this.currentUser = user.profile.name;
                this.accessTokenExpired = user.expired;
                this.isLoggedIn = (user !== null && !user.expired);
            }
        });
    }

    public getProtectedApiData() {

        const authorizationHeader = 'Authorization';
        auth.getAccessToken().then((userToken: string) => {
            axios.defaults.headers.common[authorizationHeader] = `Bearer ${userToken}`;

            axios.get('https://localhost:44341/api/DataEventRecords/')
                .then((response: any) => {
                    this.dataEventRecordsItems = response.data;
                })
                .catch((error: any) => {
                    alert(error);
                });
        });
    }
}
